.mainfold{
    background-color: var(--light-green);
}

.top-strip{
    background: var(--dark-blue-bg-gradient);
}

.strip-inner{
    height: 44px;
    align-items: center;
    justify-content: space-between;
}

.inner-left{
    align-items: center;
}

.paan-image-bg{
    background-color: var(--white-tranparent);
    border-radius: 50%;
    height: 24px;
    width: 24px;
    margin-top: 4px;
    margin-right: 16px;
}

.paan-image{
    margin-top: -10px;
    margin-left: 2px;
}

.order-text{
    color: var(--white);
}

.strip-download{
    color: var(--green);
    font-weight: 600;
}
.mainfold-location{
    padding: 32px 0;
    font-weight: 600;
    font-size: 12px;
}

.mainfold-address-1{
    color: var(--green);
}
.mainfold-address-2{
   color: var(--text-light);
   opacity: 0.45;
}
.location-arrow{
    margin: 0px 4px;
}

.mainfold-info{
    padding-bottom: 48px;
}

.mainfold-city{
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 16px;
}

.mainfold-description{
    font-size: 16px;
    font-weight: 500;
    line-height:24px;
    color: var( --text-grey);
    
}

.order-cards{
    padding-bottom: 24px;
    justify-content: space-between;
    flex-wrap: wrap;
}

.order-card-container{
    box-sizing: content-box;
    width: 240px;
    height: 180px;
    border-radius: 8px;
}

.item-cards{
    padding-bottom: 48px;
}

.item-card{
    padding: 20px 16px 16px;
    background-color: var(--whites);
    box-shadow: var(--card-shadow);
    width: 150px;
    height: 108px;
    margin: 16px;
    font-size: 12px;
    font-weight: 600px;
}

.item-card-banner{
    width: 40px;
    height: 40px;
    margin-bottom: 12px;
}